  const formatNumberWithCommas = (value) => {
    if (!value) return '';
    //    
    const numericValue = value.toString().replace(/[^0-9]/g, '');
    if (!numericValue) return '';
    return parseInt(numericValue).toLocaleString('ko-KR');
  };

